import API from './placement.api.js';

//Khi to state mc nh
const initialState = () => ({
  items: [],
  //gi tr lu tm thi dng cp nht d liu mi hoc chnh sa 
  item: {}
});

/**
 * State object
 * Use: this.$store.state.Namemodule.key
 */
const state = initialState();

/**
 * Getter functions
 * Use: this.$store.getters['Namemodule/key']
 */
const getters = {
  item: state => state.item,
  items: state => state.items,
  size: state =>  state.items.length,
  totalAmount: (state) => {
    // reduce : total gi tr ban u = 0 , lp cng dn cho ti cui mng
    return state.items.reduce((total, record) => {
      return total + (record.price * record.quantity);
    }, 0);
  },
}

/**
 * Mutations
 * this.$store.commit('Namemodule/key', data)
 */
const mutations = {
  SETUP (state, data) {
    state.items = data
  },
  SET (state, data) {
    state.item = data
  },
  ADD (state, data) {
    // kim tra cart
    let cart = state.items.find((record) => record.id === data.id);

    if (cart) {
      // sp c sn th tng s lng
      cart.quantity++;
    } else {
      // sp mi th cho vo gi
      state.items.push({
        ...data,
        stock: data.quantity, // s lng gc cn trong kho
        quantity: 1,
      });
    }
  },
  EDIT (state, data) {
    let items = state.items
    items.splice(items.indexOf(data), 1)
    state.items = items
    state.item = data
  },
  REMOVE (state, data) {
    let items = state.items
    items.splice(items.indexOf(data), 1)
  },
  DELETE (state, data) {
    let cart = state.items.find((record) => record.id === data.id);
    // gim s lng
    cart.quantity++;
  },
  CLEAN (state) {
    state.item = {}
  },

  //ci t li state
  RESET (state) {
    const newState = initialState();
    Object.keys(newState).forEach(key => {
      state[key] = newState[key]
    });
  },
}

/**
 * Actions
 * Use: this.$store.dispatch('Namemodule/key', data)
 */
const actions = {
  setup: async ({commit}) => {
    let data = await API.GET_ALL();
    commit('SETUP', data)
  },
  set: async ({commit}, data) => {
    commit('SET', data)
  },
  add: async ({commit}, data) => {
    
    // Tng 1 sp trong gi
    commit('ADD', data)
    
  },
  edit: async ({commit}, data) => {
    commit('EDIT', data)
  },
  remove: async ({commit}, data) => {
    
    // xo ht sp ca 1 mt hng trong gi
    commit('REMOVE', data)
  },
  delete: async ({commit}, data) => {
    
    // Gim 1 sp trong gi
    commit('DELETE', data)
  },
  clean: async ({commit}) => {
    commit('CLEAN')
  },
  reset: async ({ commit }) =>  {
    commit('RESET');
  },
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}